<template>
  <div class = "timing">
      <h1>计时</h1>
      <div class = 'leaf'>
         <div class="tomato-leaf leaf1"></div>
         <div class="tomato-leaf leaf2"></div>
         <div class="tomato-leaf leaf3"></div>
         <div class="peduncle"></div>
     </div>
     <div class = "clock">
        <div class = 'pie zindex' v-bind:style="{transform:'rotate('+ clock.pie2+'deg)',background:clock.bgColor}"></div>
        <div class = "pie bg" v-bind:style="{transform:'rotate('+ clock.pie1+'deg)'}"></div>
        <div class="timenum"> {{time.min}}:{{time.sec}}</div>
    </div>
    <input type= "button" @click="startTiming" class="btn btn-primary btn-block" v-bind:value="title" />
  </div>
</template>


<script>

export default {
  data () {
    return {
      title:"开始计时",
      time: {
        sec: '00',
        min: '25',
        workTime: '25',
      },
      timing: {
        isTiming: false
      },
      doneTime: {
        startTime: '',
        endTime: ''
      },
      clock: {
        pie1: 0,
        pie2: 0,
        bgColor: '#fff',
        j: 0
      },
      show: {
        bool: false,
        content: ''
      }
    }
  },
  methods: {
    startTiming(){
      if (this.timing.isTiming === false) {
        this.title = "结束计时";
        var date = new Date()
        this.doneTime.startTime = date.getHours() + ':' + this.addZero(date.getMinutes());
        this.time.min = this.time.workTime
        this.timing.isWorking = true
        this.timing.isTiming = true
        document.title = this.time.min + ':' + this.time.sec
        this.timeID = window.setTimeout(this.updateProgress, 1000)
      }else {
        this.timing.isWorking = false
        this.timing.isTiming = false
        this.time.min = this.time.workTime
        this.time.sec = '00'
        this.reset()
        window.clearTimeout(this.timeID)
      }
    },
    addZero (value) {
      if (value < 10) {
        value = '0' + value
      }
      return value + ''
    },
    updateProgress () {
      var step = this.timing.isWorking ? 6 / this.time.workTime : 6 / this.time.restTime
      this.clock.j++
      this.clock.pie1 = step * this.clock.j
      if (this.clock.pie1 === 180) {
        this.clock.pie2 = 180
        this.clock.bgColor = '#F08080'
      }
      if (this.time.min !== '00' && this.time.sec === '00') {
        this.time.min = this.addZero(+this.time.min - 1)
        this.time.sec = '59'
        this.timeID = window.setTimeout(this.updateProgress, 1000)
        document.title = this.time.min + ':' + this.time.sec
      } else if (this.time.sec !== '00') {
        this.time.sec = this.addZero(this.time.sec - 1)
        document.title = this.time.min + ':' + this.time.sec
        this.timeID = window.setTimeout(this.updateProgress, 1000)
      } 
    },
    reset () {
      this.clock.j = 0
      this.clock.pie1 = 0
      this.clock.pie2 = 0
      this.clock.bgColor = '#fff'
    }
  }
}
</script>


<style>
.leaf{
  position:relative;
  left:calc(50% - 60px);
}
.leaf .tomato-leaf {
   position:absolute;
   border-top-right-radius: 100%;
   border-bottom-left-radius: 100%;
   height: 55px;
     width: 20px;
      top: 10px;
      background:green;

}
.leaf .leaf1 {
  left:30px;
      transform:rotate(-40deg);
}
.leaf .leaf2 {
  left: 60px;
  top:0px;
    transform:rotate(50deg);
}
.leaf .leaf3 {
  left: 70px;
    transform:rotate(80deg);
}
.leaf .peduncle{
    position:absolute;
      border-radius: 20%;
     height: 50px;
     left: 50px;
     top: 0;
     width: 10px;
  background:green;
}
.clock{
  position:relative;
  font-size:54px;
  margin:64px auto 40px;
  border:8px solid #ccc;
  height:200px;
  width:200px;
  border-radius: 109px;
  box-shadow: 0 0 0 10px #ff6666;
}
.pie{  
    top:0;
    left:0;
    position:absolute;  
    width:184px;  
    height:184px;  
    border-radius:92px;  
    clip: rect(0px, 92px, 184px, 0px);
    transform:rotate(0deg);
}  

.bg {
     background:#F08080;  
     }
.zindex{
  z-index:1;
} 
.clock .timenum{
  position:absolute;
  z-index:2;
  text-align: center;
  line-height:184px;
  left:25px;
}
.settime{
  margin-top:10px;
}
.timing input[type="checkbox"] {
    position: absolute;
    clip: rect(0,0,0,0);
}
.timechoose{
  margin:10px 10px;
}
.timechoose >input[type="number"]{
 width:50px;
 margin-right:10px;
}

</style>


